<template>
  <li class="todo__item">
    <p 
        class="todo__description" 
        :class="['todo__description', {'todo__description--active': isComplete}]"
    >{{text}}</p>
    <button 
      class="todo__remove" 
      @click="$emit('remove', text)"
    ><i class="fas fa-times-circle"></i>
    </button>
  </li>
</template>

<script>
export default {
    name: "task",
    props: {
        text: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            isComplete: false,
        }
    }
};
</script>

<style lang="scss">
.todo {
  &__item {
    display: grid;
    grid-template-columns: 1fr 40px;
    align-items: start;
    background: #fff;
  }

  &__item + &__item {
    margin-top: 15px;
  }

  &__description {
    padding: 10px 10px;
    margin: 0;
    word-break: break-all;

    &--active {
      text-decoration: line-through;
      opacity: 0.3;
    }
  }

  .todo__remove {
    background: none;
    border: none;
    font-size: 1.5rem;
    color: red;
    transition: .3s ease-in-out;
    cursor: pointer;

    &:hover {
      color: #000;
    }

    &:focus {
      outline: none;
    }
  }
}
</style>